import './Price.css';

import price01 from '../../images/price-01.svg'
import price02 from '../../images/price-02.svg'
import price03 from '../../images/price-03.svg'
import price04 from '../../images/price-04.svg'

function Price() {
	return (
		<section className='price'>
			<div className='price__lines'></div>

			<div className='price__title-container'>
				<h2 className='price__title123'>    </h2>
			</div>

			<div className='price__content'>

				<div className='price__text'>
					<img className='price__image' src={price01} alt=' 1' />
					<p className='price__subtitle'>    : 8 ()        .</p>
				</div>

				<div className='price__text'>
					<img className='price__image' src={price02} alt=' 2' />
					<p className='price__subtitle'>         .</p>
				</div>

				<div className='price__text'>
					<img className='price__image' src={price03} alt=' 3' />
					<p className='price__subtitle'>                 .</p>
				</div>

				<div className='price__text'>
					<img className='price__image' src={price04} alt=' 4' />
					<p className='price__subtitle'>  -      20 %.</p>
				</div>

			</div>

			<div className='price__lines'></div>
		</section>
	)
}

export default Price;