import './Text.css';

function Text() {

	const text = [
		{ id: 0, text: '  Liebherr LR 1750       .       ' },
		{ id: 1, text: '  Liebherr LR 1750              -.' },
		{ id: 2, text: ' Liebherr LTM 1500           .        .' },
		{ id: 3, text: '          ,   -.' },
		{ id: 4, text: '        .' },
		{ id: 5, text: '         Mail.ru.     .' },
		{ id: 6, text: '         --,     Liebherr.' },
		{ id: 7, text: '             .    .' },
	]

	return (
		<section className='text'>

			<div className='text__content'>

				<h2 className='text__title'>
					  LIEBHERR  
				</h2>

				<p className='text__subtitle'>
					    .
					          ,
					  ,     ,
					 -   .
				</p>

				<h2 className='text__title'>
					  Liebherr
				</h2>

				<p className='text__subtitle'>
					Liebherr ()     .
					         .
					        .
					          .
					   ,   .
					    ,           .
				</p>

				<p className='text__subtitle'>
					       .  :
				</p>

				<ul className='text__list'>
					{
						text.map((item) => {
							return (
								<li className='text__list-item' key={item.id}>
									<p className='text__list-item-text'>
										{item.text}
									</p>
								</li>
							)
						})
					}
				</ul>

				<p className='text__subtitle'>
					   .
					    , -, ,  ,
					, , , , , ,     .
				</p>

			</div>

		</section>
	)
}

export default Text;